@import "global.scss";

:host {
  /**
   * @prop --spinner-color: The color of the spinner.
   * @prop --spinner-size: The size of the spinner.
   */
  --spinner-color: #{$rwc-color-blue-600};
  --spinner-size: 3rem;

  display: block;
}

.spinner {
  margin-left: auto;
  margin-right: auto;
  width: var(--spinner-size);
  aspect-ratio: 1;
  border-radius: 50%;
  background: radial-gradient(farthest-side, var(--spinner-color), #0000) top/8px 8px
      no-repeat,
    conic-gradient(#0000 30%, var(--spinner-color));
  mask: radial-gradient(farthest-side, #0000 calc(100% - 8px), #000 0);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 8px), #000 0);
  animation: s3 1s infinite linear;
}

@keyframes s3 {
  100% {
    transform: rotate(1turn);
  }
}
